<template>
  <el-tabs v-model="editableTabsValue" class="demo-tabs" type="border-card" v-loading="loading">
    <el-tab-pane label="状态" name="info">

      <div class="demo-collapse">
        <el-collapse v-model="infoNames">
          <el-collapse-item title="基站信息" name="1">
            <el-table :data="nowInfoList" style="width: 100%">

              <el-table-column prop="key" />

              <el-table-column prop="value" />

            </el-table>

          </el-collapse-item>


          <el-collapse-item title="基带信息" name="2">

            <el-table :data="cpuInfoList" style="width: 100%">

              <el-table-column prop="key" />

              <el-table-column prop="value" />

            </el-table>
          </el-collapse-item>

          <el-collapse-item title="电池信息" name="3">


            <el-table :data="batInfoList" style="width: 100%">

              <el-table-column prop="key" />

              <el-table-column prop="value" />

            </el-table>
          </el-collapse-item>



        </el-collapse>
      </div>






    </el-tab-pane>


    <el-tab-pane label="锁频" name="band">


      <el-divider content-position="left">LTE</el-divider>
      <el-checkbox-group v-model="band.bandLteCheckboxList" size="large">

        <el-checkbox v-for="bandLte in band.bandLteList" :key="bandLte" :label="bandLte" border />



      </el-checkbox-group>

      <el-divider content-position="left">NR</el-divider>
      <el-checkbox-group v-model="band.bandNrCheckboxList" size="large">

        <el-checkbox v-for="bandNr in band.bandNrList" :key="bandNr" :label="bandNr" border />

      </el-checkbox-group>
      <el-divider content-position="left">锁频段</el-divider>
      <el-button type="primary" style="width: 100px" @click="cleanLockBand">解除锁定</el-button>
      <el-button type="primary" style="width: 100px" @click="lockBand">锁定</el-button>

      <el-divider />



      <el-text>小圆墩锁频段功能目前处于测试阶段,存在一定风险,请在使用锁频段功能之前,使用 紫光展锐5G工具箱公开版1.0 中的 9.提取系统镜像 选项来备份所有系统镜像,其中名称有NV字符的分区,如runtimenv1,与频段锁定相关,若日后发生异常,则刷回原机备份的runtimenv1,2,即可恢复到备份镜像时设备的段锁定状态,注意:runtimenv1,2以及所有带NV字符的镜像均只支持原备份设备使用,严禁在不同设备上互刷,该行为将会使设备彻底丢失原厂校准参数!</el-text>
      <el-divider />
      <el-text>清除锁频后NR选项默认是不勾选的,不勾选代表解除锁频</el-text>






    </el-tab-pane>


    <el-tab-pane label="锁基站" name="bandStation">

      <el-input v-model="band.Technology" placeholder="Technology">
        <template #prepend> <el-text class="mx-1" style="width: 100px ;text-align:right;">Technology:</el-text>
        </template>
      </el-input>
      <el-input v-model="band.ARFCN" placeholder="ARFCN">
        <template #prepend><el-text class="mx-1" style="width: 100px ;text-align:right;">ARFCN:</el-text></template>
      </el-input>

      <el-input v-model="band.PCI" placeholder="PCI">
        <template #prepend> <el-text class="mx-1" style="width: 100px ;text-align:right;">PCI:</el-text> </template>
      </el-input>
      <el-divider content-position="left">锁基站</el-divider>
      <el-button type="primary" style="width: 100px" @click="cleanLockStation">解除锁定</el-button>
      <el-button type="primary" style="width: 100px" @click="lockStation">锁定</el-button>

      <el-divider />


      <el-table v-show="band.nrShow" :data="nrData" style="width: 100%" @current-change="nrHandleCurrentChange">
        <el-table-column prop="Band" label="Band" />
        <el-table-column prop="ARFCN" label="ARFCN" />
        <el-table-column prop="PCI" label="PCI" />
        <el-table-column prop="RSRP" label="RSRP" />
        <el-table-column prop="RSRQ" label="RSRQ" />
        <el-table-column prop="SINR" label="SINR" />
      </el-table>


      <el-table v-show="band.lteShow" :data="lteData" style="width: 100%" @current-change="lteHandleCurrentChange">
        <el-table-column prop="Band" label="Band" />
        <el-table-column prop="ARFCN" label="ARFCN" />
        <el-table-column prop="PCI" label="PCI" />
        <el-table-column prop="RSRP" label="RSRP" />
        <el-table-column prop="RSRQ" label="RSRQ" />
        <el-table-column prop="SINR" label="SINR" />
      </el-table>


    </el-tab-pane>
    <el-tab-pane label="调试" name="debug">
      <el-collapse v-model="debugNames">
        <el-collapse-item title="AT调试" name="1">


          <el-input v-model="at.imei" @input="imeiInput" maxlength="15" show-word-limit clearable="true"
            placeholder="输入需要修改的imei">


          </el-input>

          <el-input v-model="at.intput" placeholder="输入需要执行的at指令">
            <template #prepend>指令:</template>
            <template #append> <el-button @click="execAtCmd">执行</el-button></template>
          </el-input>



          <el-divider content-position="left">执行结果</el-divider>

          <el-input v-model="at.out" show-word-limit type="textarea" :rows="10" />

        </el-collapse-item>


        <el-collapse-item title="测速" name="2">

        </el-collapse-item>

        <el-collapse-item title="短信" name="3">
          <el-descriptions>
          <el-descriptions-item>
            <el-input v-model="sms.to" clearable="true" placeholder="输入号码">
              <template #prepend>发送到</template>
            </el-input>
          </el-descriptions-item>
        </el-descriptions>

        <el-input v-model="sms.text" placeholder="输入短信内容">

            <template #append> <el-button @click="sendSms">发送</el-button></template>
          </el-input>

        </el-collapse-item>



      </el-collapse>


    </el-tab-pane>
    <el-tab-pane label="设置" name="set">


      <div v-show="reg.show">
        注册码计算器可在本刷机工具目录下找到

        <el-descriptions>
          <el-descriptions-item>序列号:{{ reg.sn }}</el-descriptions-item>
        </el-descriptions>


        <el-descriptions>
          <el-descriptions-item>
            <el-input v-model="reg.key" maxlength="32" show-word-limit clearable="true" placeholder="输入注册码">
              <template #append> <el-button @click="setReg">注册</el-button></template>
            </el-input>
          </el-descriptions-item>
        </el-descriptions>

      </div>




      <el-descriptions title="充电控制" direction="vertical" border>
        <el-descriptions-item label="状态">{{ bat.state }}</el-descriptions-item>
        <el-descriptions-item label="控制"> <el-switch v-model="bat.openCharge" size="large"
            @change="setBatCharge" /></el-descriptions-item>

      </el-descriptions>




    </el-tab-pane>
  </el-tabs>
</template>




<style>
.demo-tabs>.el-tabs__content {
  padding: 32px;
  color: #6b778c;
  font-size: 12px;
  font-weight: 600;
}
</style>




<script>


import axios from 'axios'
import { ElNotification } from 'element-plus'
//import { ref } from 'vue'


export default {
  name: 'HelloWorld',
  props: {
    msg: String,
  },

  data() {
    return {
      apiUrl: "http://192.168.2.1:8080",

      nrData: [],
      lteData: [],
      nowInfoList: [],
      cpuInfoList: [],
      batInfoList: [],

      infoNames: "1",
      debugNames: "1",
      editableTabsValue: "info",
      loading: false,

      ipInfo: [],





      band: {
        bandLteList: [],
        bandLteCheckboxList: [],
        bandNrList: [],
        bandNrCheckboxList: [],
        nrShow: false,
        lteShow: false,
        ARFCN: "",
        PCI: "",
        Technology: "",

      },
      at: {
        intput: "at",
        out: "",
        imei: "",
      },
      bat: {
        openCharge: false,
        state: ""
      },
      reg: {
        show: false,
        sn: "",
        key: ""
      },
      sms: {
        to: "",
        text: "",
      }
    }
  },

  methods: {

    nrHandleCurrentChange: function (v) {
      console.log(v)
      if (v == null) return
      this.band.ARFCN = v.ARFCN
      this.band.PCI = v.PCI
      this.band.Technology = "nr"


    },
    lteHandleCurrentChange: function (v) {
      console.log(v)
      if (v == null) return
      this.band.ARFCN = v.ARFCN
      this.band.PCI = v.PCI
      this.band.Technology = "lte"
    },
    getNetwork: function () {
      console.log(window.networkTimerd)
      axios
        .get(this.apiUrl + 'api/get/network')
        .then(response => {

          console.log(response.data)
          if ((!response) || (!response.data) || (response.data.Code != 0)) {
            this.lteData = []
            this.nrData = []


            this.nowInfoList = []



            return
          }


          if (response.data.Data && response.data.Data.Technology && response.data.Data.Technology == "nr") {
            //nr
            console.log("nr")
            this.band.nrShow = true
            this.band.lteShow = false
            this.nrData = response.data.Data.Network
            for (let index = 0; index < this.nrData.length; index++) {
              this.nrData[index].Band = "N" + this.nrData[index].Band

            }
          }

          if (response.data.Data && response.data.Data.Technology && response.data.Data.Technology == "lte") {
            //nr
            console.log("lte")
            this.band.nrShow = false
            this.band.lteShow = true
            this.lteData = response.data.Data.Network
            for (let index = 0; index < this.lteData.length; index++) {
              this.lteData[index].Band = "B" + this.lteData[index].Band

            }
          }

          if (response.data.Data && response.data.Data.Cgeqosrdp) {


            //     <el-table-column prop="Band" label="Band" />
            // <el-table-column prop="ARFCN" label="ARFCN" />
            // <el-table-column prop="PCI" label="PCI" />
            // <el-table-column prop="RSRP" label="RSRP" />
            // <el-table-column prop="RSRQ" label="RSRQ" />
            // <el-table-column prop="SINR" label="SINR" />


            this.nowInfoList = [
              {
                key: "Band",
                value: response.data.Data.Network[0].Band,
              },
              {
                key: "ARFCN",
                value: response.data.Data.Network[0].ARFCN,
              },
              {
                key: "PCI",
                value: response.data.Data.Network[0].PCI,
              },
              {
                key: "RSRP",
                value: response.data.Data.Network[0].RSRP,
              },
              {
                key: "RSRQ",
                value: response.data.Data.Network[0].RSRQ,
              },
              {
                key: "SINR",
                value: response.data.Data.Network[0].SINR,
              },
              {
                key: "QCI",
                value: response.data.Data.Cgeqosrdp.Qci,
              },
              {
                key: "DL",
                value: response.data.Data.Cgeqosrdp.DL,
              },
              {
                key: "UL",
                value: response.data.Data.Cgeqosrdp.UL,
              },

            ]


          }
        })
        .catch(function (error) { // 请求失败处理
          console.log(error);
        });

    },
    startGetNetwork: function () {
      console.log(window.networkTimerd)

      if (window.networkTimerd != null) {
        clearInterval(window.networkTimerd)
      }

      window.networkTimerd = setInterval(this.getNetwork, 2000);

    },
    stopGetNetwork: function () {
      console.log(window.networkTimerd)

      if (window.networkTimerd != null) {
        clearInterval(window.networkTimerd)
      }

    },
    lockStation: function () {

      if (!this.band.Technology && this.band.Technology != "nr" && this.band.Technology != "lte") {
        ElNotification({
          title: 'Error',
          message: '参数有误',
          type: 'error',
        })
        return
      }
      console.log("锁基站:", this.band.Technology, this.band.ARFCN, this.band.PCI)

      this.loading = true
      axios.post(this.apiUrl + "api/set/station/lock",
        { "Technology": this.band.Technology, "ARFCN": this.band.ARFCN, "PCI": this.band.PCI }).then(response => {

          this.loading = false
          if ((!response) || (!response.data) || (response.data.Code != 0)) {


            ElNotification({
              title: 'Error',
              message: '锁基站执行失败' + response.data.Error,
              type: 'error',
            })

            return
          }

          ElNotification({
            title: 'Success',
            message: '锁基站执行成功',
            type: 'success',
          })

        }).catch(error => {
          this.loading = false
          console.log(error);
          ElNotification({
            title: 'error',
            message: '锁基站执行失败',
            type: 'error',
          })
        });



    },
    cleanLockStation: function () {
      console.log("解除锁基站")

      // ElNotification({
      //   title: 'error',
      //   message: '解除锁基站执行失败',
      //   type: 'error',
      // })
      // console.log(ElNotification)

      this.loading = true

      axios
        .get(this.apiUrl + 'api/set/station/cleanlock')
        .then(response => {
          this.loading = false
          console.log(response.data)


          if ((!response) || (!response.data) || (response.data.Code != 0)) {
            ElNotification({
              title: 'Error',
              message: '解除锁基站执行失败' + response.data.Error,
              type: 'error',
            })
            return
          }

          ElNotification({
            title: 'Success',
            message: '解除锁基站执行成功',
            type: 'success',
          })

        }).catch(error => {
          this.loading = false
          console.log(error);
          ElNotification({
            title: 'error',
            message: '解除锁基站执行失败',
            type: 'error',
          })
        });


    }
    ,
    getBandInfo: function () {
      //{"Code":0,"Error":"","Data":{"TDD_41":true,"TDD_40":true,"TDD_39":true,"TDD_38":true,"TDD_34":true,"FDD_28":false,"FDD_20":false,"FDD_08":true,"FDD_07":false,"FDD_05":true,"FDD_03":true,"FDD_02":false,"FDD_01":true,"N28":false,"N01":false,"N79":false,"N78":false,"N77":false,"N41":false}}


      this.band.bandLteList = []
      this.band.bandLteCheckboxList = []

      this.band.bandNrList = []
      this.band.bandNrCheckboxList = []
      axios
        .get(this.apiUrl + 'api/get/band/info')
        .then(response => {

          console.log(response.data)

          if ((!response) || (!response.data) || (response.data.Code != 0)) {
            console.log(response.data.Error)

            if (response.data.Code === 10000) {

              ElNotification({
                title: 'Error',
                message: '当前未注册',
                type: 'error',
              })

              this.editableTabsValue = "set"
              this.getReg()
              this.reg.show = true


            }

            return
          }





          for (const key in response.data.Data) {
            if (Object.hasOwnProperty.call(response.data.Data, key)) {

              if (key.indexOf("N")) {
                this.band.bandLteList.push(key)
                if (response.data.Data[key]) {
                  this.band.bandLteCheckboxList.push(key)
                }
              } else {
                this.band.bandNrList.push(key)
                if (response.data.Data[key]) {
                  this.band.bandNrCheckboxList.push(key)
                }
              }


            }
          }





        }).catch(error => {
          console.log(error);

        });

    },
    lockBand: function () {

      console.log("lte1:", this.band.bandLteList)

      console.log("lte2:", this.band.bandLteCheckboxList)
      console.log("nr1:", this.band.bandNrList)
      console.log("nr2:", this.band.bandNrCheckboxList)

      var data = Object.create(null);

      for (let index = 0; index < this.band.bandLteList.length; index++) {
        data[this.band.bandLteList[index]] = false

      }

      for (let index = 0; index < this.band.bandLteCheckboxList.length; index++) {
        data[this.band.bandLteCheckboxList[index]] = true
      }


      for (let index = 0; index < this.band.bandNrList.length; index++) {
        data[this.band.bandNrList[index]] = false
      }
      for (let index = 0; index < this.band.bandNrCheckboxList.length; index++) {
        data[this.band.bandNrCheckboxList[index]] = true
      }




      console.log("锁频1:", data)


      console.log("锁频2:", JSON.stringify(data))

      this.loading = true
      axios.post(this.apiUrl + "api/set/band/lock",
        data).then(response => {

          this.loading = false
          if ((!response) || (!response.data) || (response.data.Code != 0)) {


            ElNotification({
              title: 'Error',
              message: '锁频执行失败' + response.data.Error,
              type: 'error',
            })

            return
          }

          ElNotification({
            title: 'Success',
            message: '锁频执行成功',
            type: 'success',
          })

        }).catch(error => {
          this.loading = false
          console.log(error);
          ElNotification({
            title: 'error',
            message: '锁频执行失败',
            type: 'error',
          })
        });




    },
    cleanLockBand: function () {

      console.log("解除锁频")
      this.loading = true
      axios
        .get(this.apiUrl + 'api/set/band/cleanlock')
        .then(response => {
          this.loading = false
          console.log(response.data)

          if ((!response) || (!response.data) || (response.data.Code != 0)) {
            ElNotification({
              title: 'Error',
              message: '解除锁频执行失败' + response.data.Error,
              type: 'error',
            })
            return
          }

          ElNotification({
            title: 'Success',
            message: '解除锁频执行成功',
            type: 'success',
          })
          this.getBandInfo()

        }).catch(error => {
          this.loading = false
          console.log(error);
          ElNotification({
            title: 'error',
            message: '解除锁频执行失败',
            type: 'error',
          })
        });



    },

    getSysinfo: function () {

      axios
        .get(this.apiUrl + 'api/get/sysinfo')
        .then(response => {

          console.log(response.data)

          if ((!response) || (!response.data) || (response.data.Code != 0)) {
            console.log(response.data.Error)

            return
          }
          this.batInfoList = []
          this.cpuInfoList = []
          for (const key in response.data.Data) {
            if (Object.hasOwnProperty.call(response.data.Data, key)) {


              var info = {}
              switch (key) {
                case "CPU_TEMP":


                  for (let i = 0; i < response.data.Data[key].length; i++) {
                    info = { key: "基带温度" + i, value: (response.data.Data[key][i] / 1000).toFixed(2) }
                    this.cpuInfoList.push(info);
                  }


                  break

                // case "POWER_SUPPLY_NAME":

                //   break

                case "POWER_SUPPLY_STATUS":
                  info = { key: "充电状态", value: response.data.Data[key] }
                  this.batInfoList.push(info);
                  this.bat.state = response.data.Data[key]
                  if (response.data.Data[key] === "Full") {
                    this.bat.openCharge = true

                  }
                  //Discharging 没连接电源

                  if (response.data.Data[key] === "Discharging") {
                    this.bat.openCharge = true
                  }
                  //Charging 充电
                  if (response.data.Data[key] === "Charging") {
                    this.bat.openCharge = true
                  }

                  //Not charging 没有充电
                  if (response.data.Data[key] === "Not charging") {
                    this.bat.openCharge = false
                  }
                  break

                case "POWER_SUPPLY_HEALTH":
                  info = { key: "电池状态", value: response.data.Data[key] }
                  this.batInfoList.push(info);


                  break
                case "POWER_SUPPLY_PRESENT":
                  // info = { key: "电量百分比", value: response.data.Data[key] }
                  // this.batInfoList.push(info);
                  console.log(response.data.Data[key])
                  break
                case "POWER_SUPPLY_VOLTAGE_NOW":
                  info = { key: "当前电压(mv)", value: response.data.Data[key] / 1000 }
                  this.batInfoList.push(info);
                  break
                case "POWER_SUPPLY_VOLTAGE_AVG":
                  info = { key: "平均电压(mv)", value: response.data.Data[key] / 1000 }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_CAPACITY":
                  info = { key: "电池容量", value: response.data.Data[key] }
                  this.batInfoList.push(info);
                  break
                case "POWER_SUPPLY_CAPACITY_LEVEL":
                  info = { key: "容量状态", value: response.data.Data[key] }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_ONLINE":
                  info = { key: "电源状态", value: response.data.Data[key] }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_CHARGE_FULL":
                  info = { key: "总容量(mah)", value: response.data.Data[key] / 1000 }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_CONSTANT_CHARGE_CURRENT":
                  info = { key: "电源恒定充电电流(ma)", value: (response.data.Data[key] / 1000).toFixed(0) }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_INPUT_CURRENT_LIMIT":
                  info = { key: "充电限制电流(ma)", value: (response.data.Data[key] / 1000).toFixed(0) }
                  this.batInfoList.push(info);
                  break
                //POWER_SUPPLY_CURRENT_NOW

                case "POWER_SUPPLY_CURRENT_NOW":
                  info = { key: "当前电流(ma)", value: (response.data.Data[key] / 1000).toFixed(0) }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_CURRENT_AVG":
                  info = { key: "平均电流(ma)", value: (response.data.Data[key] / 1000).toFixed(0) }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_CHARGE_COUNTER":
                  info = { key: "当前容量(mah)", value: (response.data.Data[key] / 1000).toFixed(0) }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_CHARGE_CONTROL_LIMIT":
                  info = { key: "电流最大值(ma)", value: (response.data.Data[key] / 1000).toFixed(0) }
                  this.batInfoList.push(info);
                  break

                // case "POWER_SUPPLY_TIME_TO_FULL_NOW":
                //   info = { key: "预计充满时间(分钟)", value: response.data.Data[key] }
                //   this.batInfoList.push(info);
                //   break
                case "POWER_SUPPLY_CYCLE_COUNT":
                  info = { key: "电池循环次数", value: response.data.Data[key] }
                  this.batInfoList.push(info);
                  break
                case "POWER_SUPPLY_CURRENT_MAX":
                  info = { key: "电源最大电流(ma)", value: (response.data.Data[key] / 1000).toFixed(0) }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_VOLTAGE_MAX":
                  info = { key: "电压最大值(mv)", value: (response.data.Data[key] / 1000).toFixed(0) }
                  this.batInfoList.push(info);
                  break

                case "POWER_SUPPLY_TEMP":
                  info = { key: "电池温度", value: (response.data.Data[key] / 10).toFixed(1) }
                  this.batInfoList.push(info);
                  break



              }
            }
          }





        }).catch(error => {
          console.log(error);

        });

    },

    startGetSysinfo: function () {
      console.log(window.sysInfoTimerd)

      if (window.sysInfoTimerd != null) {
        clearInterval(window.sysInfoTimerd)
      }

      window.sysInfoTimerd = setInterval(this.getSysinfo, 2000);

    },
    stopGetSysinfo: function () {
      console.log(window.sysInfoTimerd)

      if (window.sysInfoTimerd != null) {
        clearInterval(window.sysInfoTimerd)
      }

    },

    execAtCmd: function () {
      if (!this.at && !this.at.intput) {
        ElNotification({
          title: 'Error',
          message: 'AT参数有误',
          type: 'error',
        })
        return
      }

      if (this.at.intput == undefined) {
        ElNotification({
          title: 'Error',
          message: 'AT参数有误',
          type: 'error',
        })
        return
      }
      console.log(this.at.intput)

      this.loading = true
      this.at.out += this.at.intput + "\r\n"
      axios.post(this.apiUrl + "api/exec/atcmd",
        { "Method": "execat", "Cmd": this.at.intput }).then(response => {

          this.loading = false
          if ((!response) || (!response.data) || (response.data.Code != 0)) {


            this.at.out += response.data.Error + "\r\n"

            return
          }




          var s = response.data.Data + ""
          while (s.slice(0, 2) === "\r\n") {
            s = s.slice(2)
          }


          this.at.out += s




        }).catch(error => {
          this.loading = false
          console.log(error);

          this.at.out += error + "\r\n"
        });


    },
    imeiInput: function () {
      if (this.at.imei === "") {
        this.at.intput = ""
      } else {
        this.at.intput = "AT+SPIMEI=0,\"" + this.at.imei + "\""
      }

    },
    setBatCharge: function () {

      axios.post(this.apiUrl + "api/set/charge/state",

        { "CHARGE_STATE": this.bat.openCharge }).then(response => {

          if ((!response) || (!response.data) || (response.data.Code != 0)) {
            return
          }
        }).catch(error => {
          console.log(error);

        });

    },
    getReg: function () {
      axios
        .get(this.apiUrl + 'api/get/reg')
        .then(response => {

          if ((!response) || (!response.data) || (response.data.Code != 0)) {
            console.log(response.data.Error)

            return
          }
          this.reg.sn = response.data.Data

        }).catch(error => {
          console.log(error);
        });

    },
    setReg: function () {
      axios.post(this.apiUrl + "api/set/reg",

        { "Key": this.reg.key }).then(response => {

          if ((!response) || (!response.data) || (response.data.Code != 0)) {
            return
          }
          location.reload();


        }).catch(error => {
          console.log(error);

        });
    },
    showError: function (msg) {
      ElNotification({
          title: 'Error',
          message: msg,
          type: 'error',
        })
    },
    showSuccess: function (msg) {
      ElNotification({
            title: 'Success',
            message: msg,
            type: 'success',
          })
    },
    sendSms: function () {
      if (this.sms.to === "") {
        return
      }
      if (this.sms.text === "") {
        return
      }

      axios.post(this.apiUrl + "api/set/send/sms",

        { "To": this.sms.to, "Text": this.sms.text }).then(response => {

          if ((!response) || (!response.data) || (response.data.Code != 0)) {

            this.showError("发送短信失败:"+response.data.Error)
            return
          }
          this.showSuccess("发送短信成功")


        }).catch(error => {
          console.log(error);
          this.showError("发送短信失败:"+error)

        });


    },


  },

  mounted() {
      this.apiUrl=window.location.href

   // this.apiUrl = "http://192.168.2.1:8080/"
    this.getNetwork()
    this.getBandInfo()
    this.getSysinfo()

    this.startGetNetwork()
    this.startGetSysinfo()

  }

}
// console.log(app)


